import axios from 'axios';

export default {
  data() {
    return {
      noData: false,
      candidates: [],
      noteDialog: false,
      noteName: '',
      status: ['Scouting','Technical interview','Preliminary interview','Assessment'],
    }
  },
  mounted() {
    this.readData();
  },
  methods: {
    readData() {
      this.loader = true;
      let url = 'https://api.sheety.co/ec400a6bb2ac250558c262e5fab58060/hfarmData/candidates';
      var list = [];
    
      axios.get(url).then(response => {
        if (this.slug) {
          response.data.candidates.forEach(element => {
            if (element.application.includes(this.slug) && element.client.includes(this.type) && element.availability != 'Refused')
              list.push(element);
          });
        } else {
          response.data.candidates.forEach(element => {
            list.push(element);
          });
        }

        this.candidates = list;

        if (list.length > 0)
          this.noData = false;
        else
          this.noData = true;
      });
    },

    openDialog (opt, name) {
      this.noteDialog = opt;
      this.noteName = name;
    },

    removeCandidate(id) {
      let url = 'https://api.sheety.co/ec400a6bb2ac250558c262e5fab58060/hfarmData/candidates/' + id;
      
      axios.delete(url).then((response) => {
        this.readData();
      }, (error) => {
        console.log(error);
      });
    },

    editStatusCandidate(obj) {
      let url = 'https://api.sheety.co/ec400a6bb2ac250558c262e5fab58060/hfarmData/candidates/' + obj.id;
      
      axios.put(url, {
        candidate: obj
      })
      .then((response) => {
        this.readData();
      }, (error) => {
        console.log(error);
      });
    }
  },
  async asyncData({ params }) {
    const slug = params.slug
    const type = params.type

    return { slug, type }
  }
}
